@import "./scss/reset";
@import "./scss/variables";
.app {
	// background-color: #e3e3;
	font-family: "Nunito", sans-serif;
	height: 100vh;
	color: $colorMain;
	overflow-x: hidden;
}
.main {
	background-color: #fff;

	display: flex;

	position: relative;
}
.container {
	max-width: calc(1280px + 10px);
	padding: 0 5px;
	margin: 0 auto;
	height: 100%;
	width: 100%;
	background-color: $bgMain;

	@media (min-width: 768px) {
		max-width: calc(1280px + 30px);
		padding: 0 15px 0 5px;
	}
}
.wrapper {
	width: 100%;
	padding: 10px 0;
}
