@import "../../scss/variables";

.sort {
	position: relative;
	z-index: 20;
	height: 100%;
}
.title {
	span {
		color: $colorTitle;
		cursor: pointer;
	}
}
.popup {
	position: absolute;
	right: -100vw;
	width: 100%;

	background-color: #fff;
	box-shadow: 0px 2px 5px 0px rgba(34, 60, 80, 0.5);
	border-radius: 10px;
	overflow: hidden;

	transition: right 0.3s ease 0s;

	&.active {
		right: 0;
	}
	li {
		padding: 12px;
		transition:
			background-color,
			color,
			font-weight 0.3s ease 0s;

		&.active {
			background-color: $colorTitle;
			color: #fff;
			font-weight: 800;
		}
	}
}
