@import "../../scss/variables";

.aside {
	width: 100%;
	height: 100%;
	overflow: auto;

	display: flex;

	position: absolute;
	top: -100%;
	z-index: 100;

	transition: top 0.3s ease 0s;

	&.active {
		top: 0;
	}

	@media (min-width: 768px) {
		width: 35%;
		position: sticky;
		// border-right: 1px solid $bgLine;
		// border-bottom: 1px solid $bgLine;
	}
	@media (min-width: 1024px) {
		// position: sticky;
		top: 0;
		width: 30%;
	}
}
.items {
	flex: 0 0 100%;

	display: flex;
	flex-direction: column;
	gap: 10px;
	background-color: $bgWhitePearl;
	height: 100%;
	// width: 100%;

	// position: absolute;
	// left: 0;
	// transition: left 0.3s ease 0s;

	// &.active {
	// 	left: 0;
	// }

	// flex: 0 0 60%;
	padding: 10px 10px 30px;

	@media (min-width: 425px) {
		flex: 0 0 70%;
	}

	@media (min-width: 500px) {
		// flex: 0 0 100%;
		flex: 0 0 50%;
	}
	@media (min-width: 768px) {
		// flex: 0 0 100%;
		flex: 0 0 100%;
	}

	li {
		a {
			display: block;
			box-shadow: 0px 2px 2px 0px rgba(34, 60, 80, 0.2);
			padding: 12px 10px;
			background-color: $bgCategory;
			border-radius: 10px;
			font-size: 16px;
			color: rgba($color: $colorAside, $alpha: 0.8);
			transition: background-color 0.3s ease 0s;

			&.active {
				background-color: $bgCategoryHover;
			}

			img {
				width: 15px;
				height: 15px;
				margin-right: 10px;
			}
		}
	}
}
.shadow {
	height: 100%;
	width: 100%;
	background-color: rgba($color: #000000, $alpha: 0.4);
	@media (min-width: 768px) {
		display: none;
	}
}
